<template>
  <div class="widget widget-dashboard">
    <h3 class="widget-title">My Account</h3>

    <ul class="list">
      <li class=""><router-link to="/user/dashboard">Account Dashboard</router-link></li>
      <li><router-link to="/user/orders">My Orders</router-link></li>
      <li><router-link to="/user/wishlist">My Wishlist</router-link></li>
      <li><a href="#" @click="logoutUser">Logout</a></li>
    </ul>
  </div>
  <!-- End .widget -->
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { USER_SIGNOUT } from "../../config/constants";
import api from "../../http-common";
export default {
  name: "DashboardSidebar",
  data() {
    return {
      old_password: "",
      password: "",
      confirm_password: "",
      btnDisabled: false,
    };
  },
  computed: {
    ...mapGetters("account", ["isLoggedIn"]),
    ...mapActions("account", ["logout"]),
  },
  methods: {
    async logoutUser() {
      if (this.isLoggedIn) {
        const response = await api.execute(USER_SIGNOUT, USER_SIGNOUT.route);
        if (response && response.success) {
          this.logout;
          this.$toast.success(response.message);
          this.$router.push({ name: "Login" });
        } else {
          this.logout;
          this.$toast.error("Else User Logout");
          this.$router.push({ name: "Login" });
        }
      } else {
        this.$toast.error("You are not logged in user!");
        this.$router.push({ name: "Login" });
      }
    },
  },
};
</script>
