<template>
  <section class="featured-products-section">
    <div id="v-carousel" class="custom-products">
      <div class="container">
        <h2 class="section-title heading-border ls-20 border-0">
          Breaking Today
        </h2>
        <div class="card-carousel-wrapper">
          <div
            class="card-carousel--nav__left"
            @click="moveCarousel(-1)"
            :disabled="atHeadOfList"
          ></div>
          <div class="card-carousel">
            <div class="card-carousel--overflow-container">
              <div
                class="card-carousel-cards"
                :style="{
                  transform: 'translateX' + '(' + currentOffset + 'px' + ')',
                }"
              >
                <div
                  class="card-carousel--card product-default"
                  v-for="(item, index) in items"
                  :key="index"
                >
                  <figure>
                    <router-link
                      :to="{ name: 'product', params: { slug: item.slug } }"
                    >
                      <img :src="$helpers.getImageUrl(item.product_image)" />
                    </router-link>
                    <div class="label-group">
                      <!-- <div
                        class="product-label label-sale"
                        title="Product Views"
                      >
                        {{ item.no_views }} <i class="fa fa-eye"></i>
                      </div> -->
                    </div>
                  </figure>
                  <div class="product-details">
                    <h3 class="product-title">
                      <a href="javascript:void(0)">{{ item.product_name }}</a>
                    </h3>
                    <div class="price-box">
                      <!-- <del class="old-price">$59.00</del> -->
                      <span class="product-price">{{
                        item.price_range == null
                          ? "$" + item.price
                          : item.price_range
                      }}</span>
                    </div>
                    <!-- End .price-box -->
                    <div class="product-action">
                      <AddToWishListBtn :product_id="item.id" :is_wishlist_product="item.is_wishlist_product"/>
                      <router-link
                        :to="{ name: 'product', params: { slug: item.slug } }"
                        class="btn-icon btn-add-cart c-primary-bg"
                        >VIEW PRODUCT</router-link
                      >
                      <router-link
                        :to="{ name: 'product', params: { slug: item.slug } }"
                        class="btn-quickview"
                        :title="showProductViews(item.no_views)"
                        ><i class="fa fa-eye"></i
                      ></router-link>
                    </div>
                  </div>
                  <!-- <i class="fas fa-external-link-alt"></i> -->
                  <!-- <div class="card-carousel--card--footer">
                                    <p>{{ item.name }}</p>
                                </div> -->
                </div>
              </div>
            </div>
          </div>
          <div
            class="card-carousel--nav__right"
            @click="moveCarousel(1)"
            :disabled="atEndOfList"
          ></div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import { mapGetters } from "vuex";
import AddToWishListBtn from "./AddToWishListBtn.vue";
export default {
  name: "FeaturedProductSlider",
  components: { AddToWishListBtn },
  data() {
    return {
      currentOffset: 0,
      windowSize: 4,
      paginationFactor: 280,
      items: [
        {
          id: 1,
          product_name: "Blue",
          product_image: "/assets/images/products/product-1.jpg",
        },
        {
          id: 2,
          product_name: "New Product",
          product_image: "/assets/images/products/product-2.jpg",
        },
        {
          id: 3,
          product_name: "2 Color Football",
          product_image: "/assets/images/products/product-3.jpg",
        },
        {
          id: 4,
          product_name: "no offer product",
          product_image: "/assets/images/products/product-4.jpg",
        },
        {
          id: 5,
          product_name: "Style Bootiques",
          product_image: "/assets/images/products/product-1.jpg",
        },
      ],
    };
  },
  computed: {
    ...mapGetters("account", ["user", "isLoggedIn", "getUserId"]),
    ...mapGetters("product", ["featuredProducts"]),
    atEndOfList() {
      return (
        this.currentOffset <=
        this.paginationFactor * -1 * (this.items.length - this.windowSize)
      );
    },
    atHeadOfList() {
      return this.currentOffset === 0;
    },
  },
  created() {
    this.getWindowWidth();
    this.items = this.featuredProducts;
  },
  methods: {
    moveCarousel(direction) {
      // Find a more elegant way to express the :style. consider using props to make it truly generic
      if (direction === 1 && !this.atEndOfList) {
        this.currentOffset -= this.paginationFactor;
      } else if (direction === -1 && !this.atHeadOfList) {
        this.currentOffset += this.paginationFactor;
      }
    },
    getWindowWidth() {
      if (window.innerWidth <= 420) {
        this.windowSize = 1;
        this.windowSize;
        this.paginationFactor = 280;
      } else if ((window.innerWidth > 420) & (window.innerWidth <= 768)) {
        this.windowSize = 3;
        this.paginationFactor = 240;
      } else {
        this.windowSize;
      }
    },
    showProductViews(view_count) {
      return view_count + " Views";
    },
  },
};
</script>
