<template>
  <div
    class="header-icon header-icon header-search header-search-inline header-search-category w-lg-max text-right"
  >
    <a href="#" class="search-toggle" role="button"
      ><i class="icon-search-3"></i
    ></a>
    <form action="javascript:void(0)" method="get">
      <div class="header-search-wrapper">
        <input
          type="search"
          class="form-control"
          name="q"
          id="q"
          placeholder="Search..."
          required
        />
        <div class="select-custom" v-if="show">
          <select id="cat" name="cat"  v-model="selectedCategory">
            <option value="all" selected>All Categories</option>
            <option
              v-for="item in items"
              :key="item.id"
              :value="item.category_name"
              >{{ item.category_name }}</option>
          </select>
        </div>
        <!-- End .select-custom -->
        <button class="btn icon-search-3 p-0" type="submit"></button>
      </div>
      <!-- End .header-search-wrapper -->
    </form>
  </div>
  <!-- End .header-search -->
</template>
<script>
import { mapGetters } from "vuex";
export default {
    name: "HeaderSearchForm",
    data() {
        return {
            items:[],
            show:false,
            selectedCategory:"all"
        };
    },
    watch : {
       categories: {
          handler: function (val, oldVal) {
            console.log(oldVal)
                 this.items = val; 
                 this.show = true;
          },
          deep: true,
        }
    },
    computed: {
      ...mapGetters("product", ["categories"]),
    },
}
</script>
