<template>
  <a
    href="javascript:void(0)"
    @click="addToWishlist(product_id)"
    class="btn-icon-wish"
    title="Add to wishlist"
    ><i :class="wishListClassToggle"></i
  ></a>
</template>
<script>
import api from "../../http-common";
import {
  ADD_REMOVE_WISHLIST,
  ERROR_SOMETHING_WENT_WRONG,
} from "../../config/constants";
import { mapGetters } from "vuex";
export default {
  name: "AddToWishListBtn",
  props: ["product_id","is_wishlist_product"],
  data() {
    return {
      item_id: this.product_id,
      is_wishlist: this.is_wishlist_product,
    };
  },
  computed: {
    ...mapGetters("account", ["user", "isLoggedIn", "getUserId"]),
    wishListClassToggle: function () {
        return this.is_wishlist == 1?"icon-heart active":"icon-heart ";
    }
  },
  created() {},
  methods: {
    async addToWishlist(product_id) {
      if (this.isLoggedIn && this.getUserId != null) {
        let payload = {
          product_id: product_id,
        };
        const response = await api.execute(
          ADD_REMOVE_WISHLIST,
          ADD_REMOVE_WISHLIST.route,
          payload
        );
        if (response && response.success == 1) {
          if(response.data.is_wishlist == 1){
            this.is_wishlist = 1;
          }else{
            this.is_wishlist = 0;
          }
          this.$toast.success(response.message);
          
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
        } else {
          this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
        }
      } else {
        this.$toast.error("Please Login to your account.");
        this.$router.push({ name: "Login" });
      }
    },
  },
};
</script>
