<template>
  <form
    action="#"
    class="change-address-form"
    @submit.prevent="updateUserProfile"
  >
    <div class="row">
      <div class="col-sm-12">
        <div class="row">
          <div class="col-md-6 col-sm-12">
            <div class="form-group required-field">
              <label for="acc-name">Full Name</label>
              <input
                type="text"
                class="form-control"
                id="acc-name"
                name="acc-name"
                placeholder="Enter Your Full Name"
                required=""
                v-model="full_name"
              />
            </div>
          </div>
          <div class="col-md-2 col-sm-3">
            <div class="form-group">
              <label for="acc-phone-code">Code</label>
              <input
                type="text"
                class="form-control"
                id="acc-phone-code"
                name="acc-phone-code"
                value="+1"
                readonly
              />
            </div>
          </div>
          <div class="col-md-4 col-sm-7">
            <div class="form-group">
              <label for="acc-phone">Phone Number</label>
              <div class="tag-input">
                <!-- <span class="phonecode">+1</span> -->
                <input
                  type="text"
                  class="form-control"
                  id="acc-phone"
                  name="acc-phone"
                  placeholder="Enter Your Phone Number"
                  v-model="phone_number"
                />
              </div>
            </div>
          </div>
        </div>
        <!-- End .row -->
      </div>
    </div>
    <!-- End .row -->
    <div class="row">
      <div class="col-sm-12 col-md-12">
        <div class="form-group required-field">
          <label for="acc-full-address">Full Address</label>
          <input
            type="text"
            class="form-control"
            id="acc-full-address"
            name="acc-full-address"
            required=""
            placeholder="Enter Your Full Address"
            v-model="full_address"
          />
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-sm-12 col-md-4">
        <div class="form-group required-field">
          <label for="acc-state">State</label>
          <select
            class="form-control"
            id="acc-state"
            name="acc-state"
            v-model="selectedState"
          >
            <option
              v-for="us_st in selectedOptionStates"
              :key="us_st.id"
              :selected="us_st.name == selectedState"
              :value="us_st.name"
              >{{ us_st.name }}</option
            >
          </select>
        </div>
      </div>
      <div class="col-sm-12 col-md-4">
        <div class="form-group required-field">
          <label for="acc-city">City</label>
          <select
            class="form-control"
            id="acc-city"
            name="acc-city"
            v-model="selectedCity"
          >
            <option
              v-for="us_city in selectedOptionCities"
              :key="us_city.id"
              :selected="us_city.name == selectedCity"
              :value="us_city.name"
              >{{ us_city.name }}</option
            >
          </select>
        </div>
      </div>
      <div class="col-sm-12 col-md-4">
        <div class="form-group required-field">
          <label for="acc-zipcode">Zipcode</label>
          <input
            type="text"
            class="form-control"
            id="acc-zipcode"
            name="acc_zipcode"
            v-model="zipcode"
            required=""
          />
        </div>
      </div>
    </div>

    <div class="mb-2"></div>
    <!-- margin -->
    <div class="form-footer">
      <div class="form-footer">
        <button
          type="submit"
          class="btn btn-primary c-primary-bg"
          :disabled="btnDisabled ? true : false"
        >
          Update Information
        </button>
      </div>
    </div>
    <!-- End .form-footer -->
  </form>
</template>
<script>
import { mapActions } from "vuex";
import api from "../../http-common";
import { USER_PROFILE_UPDATE } from "../../config/constants";
import us_states_json from "../../config/us_states_and_cities.json";
export default {
  name: "ChangeAddressForm",
  props: ["userData"],
  data() {
    return {
      user_id: this.userData.uid,  
      full_name: this.userData.full_name,
      phone_number: this.userData.phone,
      full_address: this.userData.address,
      zipcode: this.userData.zipcode,
      btnDisabled: false,
      usStates: us_states_json,
      selectedOptionStates: us_states_json.states,
      selectedState: this.userData.state,
      selectedCity: this.userData.city,
      selectedOptionCities: [],
    };
  },
  methods: {
    ...mapActions("account", ["saveUser"]),
    async updateUserProfile() {
      if (this.full_name == "" || this.full_name.length < 3) {
        this.$toast.error("Please Enter Valid Name");
        return false;
      } else if (this.phone_number == "" || this.phone_number.length < 7) {
        this.$toast.error("Please Enter Valid Phone number");
      } else if (this.full_address == "" || this.full_address.length < 10) {
        this.$toast.error("Please Enter Valid Address");
      } else if (this.zipcode == "" || this.zipcode.length < 3) {
        this.$toast.error("Please Enter Valid Zipcode");
      } else {
        this.btnDisabled = true;
        let profileData = {
          full_name: this.full_name,
          phone: this.phone_number,
          address: this.full_address,
          state: this.selectedState,
          city: this.selectedCity,
          zipcode: this.zipcode,
        };
        const response = await api.execute(
          USER_PROFILE_UPDATE,
          USER_PROFILE_UPDATE.route+this.user_id,
          profileData
        );
        if (response && response.success == 1) {
          this.saveUser(response.data);
          this.$toast.success(response.message);
          this.$emit('custom-address-toggle',false);
          this.btnDisabled = false;
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
          this.btnDisabled = false;
        } else {
          this.$toast.error("Something goes wrong");
          this.btnDisabled = false;
        }
      }
    },
    getSelectedCities() {
      let needle = this.selectedState;
      for (var i = 0; i < this.selectedOptionStates.length; i++) {
        // console.log(this.selectedOptionStates[i].name)
        if (this.selectedOptionStates[i].name == needle) {
          this.selectedOptionCities = this.selectedOptionStates[i].cities;
        }
      }
    },
  },
  watch: {
    selectedState: function() {
      //   console.log(this.selectedState);
      this.selectedOptionCities = [];
      var needle = this.selectedState;
      for (var i = 0; i < this.selectedOptionStates.length; i++) {
        if (this.selectedOptionStates[i].name == needle) {
          if (this.selectedOptionStates[i].cities.length > 0) {
            this.selectedOptionCities = this.selectedOptionStates[i].cities;
            this.selectedCity = this.selectedOptionStates[i].cities[0].name;
          } else {
            this.selectedOptionCities = [{ id: 0, name: "No City Available" }];
            this.selectedCity = "No City Available";
          }
          break;
        }
      }
    },
  },
  mounted() {
    this.getSelectedCities();
  },
};
</script>
<style scoped>
.login-heading {
  margin-bottom: 3rem;
}
.change-address-form .form-control,
.change-address-form select.form-control {
  height: 4rem;
  padding: 10px 10px;
}
.change-address-form .btn {
  padding: 1.2rem 1.2rem;
}
.change-address-form .form-footer button {
  font-size: 12px;
}
form.change-address-form {
  margin-bottom: 0px !important;
  padding: 14px 0px;
}
form.change-address-form .form-footer {
  margin-bottom: 10px;
}
</style>
