<template>
  <nav aria-label="breadcrumb" class="breadcrumb-nav">
    <div class="container">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link to="/user/orders"><i class="icon-home"></i></router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">
          Order Details
        </li>
      </ol>
    </div>
    <!-- End .container -->
  </nav>

  <div class="container">
    <div class="row">
      <div class="col-lg-9 order-lg-last dashboard-content">
        <h2>Order Details</h2>
        <div class="row" v-if="order">
          <div class="col-sm-12 col-md-6">
            <div class="card  mb-3">
              <div class="card-header">Order Number: #{{ order.order_number }}</div>
              <div class="card-body">
                <p class="card-text">
                    <table class="table">
                        <tr>
                            <th>Order Date</th>
                            <td>{{ orderDate() }}</td>
                        </tr>
                        <tr>
                            <th>SubTotal</th>
                            <td>${{ order.item_total }}</td>
                        </tr>
                        <tr>
                            <th>Discount</th>
                            <td>${{ order.discount }}</td>
                        </tr>
                        <tr>
                            <th>Grand Total</th>
                            <td>${{ order.total_amount }}</td>
                        </tr>
                         <tr>
                            <th>Order Status</th>
                            <td>{{ order.order_status }}</td>
                        </tr>
                         <tr>
                            <th>Payment Status</th>
                            <td>{{ order.payment_status }}</td>
                        </tr>
                    </table>
                </p>
              </div>
            </div>
          </div>
           <div class="col-sm-12 col-md-6">
            <div class="card  mb-3">
              <div class="card-header">Shipping Information</div>
              <div class="card-body">
                <p class="card-text">
                    <table class="table" v-if="order.shipping_information">
                        <tr>
                            <th>Name</th>
                            <td>{{ order.shipping_information.full_name }}</td>
                        </tr>
                        <tr>
                            <th>Phone</th>
                            <td>{{ order.shipping_information.phone }}</td>
                        </tr>
                        <tr>
                            <th>Address</th>
                            <td>{{ order.shipping_information.address }}</td>
                        </tr>
                        <tr>
                            <th>State</th>
                            <td>{{ order.shipping_information.state }}</td>
                        </tr>
                        <tr>
                            <th>City</th>
                            <td>{{ order.shipping_information.city }}</td>
                        </tr>
                        <tr>
                            <th>Zipcode</th>
                            <td>{{ order.shipping_information.zipcode }}</td>
                        </tr>
                    </table>
                </p>
              </div>
            </div>
          </div>
            <div class="col-sm-12 col-md-12">
            <div class="card mb-3">
              <div class="card-header">Redeem Points & Discount Voucher</div>
              <div class="card-body">
                <table class="table">
                    <thead>
                        <tr>
                        <th scope="col">Redeem Points</th>
                        <th scope="col">Points Value</th>
                        <th scope="col">Promocode</th>
                        <th scope="col">Promocode Value</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{{ (order.point_used == 0?"No Any":order.point_used) }}</td>
                            <td>${{ order.point_value }}</td>
                            <td>{{ (order.promocode == null?"No Any":order.promocode) }}</td>
                            <td>${{ order.promocode_value }}</td>
                        
                        </tr>
                    </tbody>
                </table>
              </div>
            </div>
          </div>
           <div class="col-sm-12 col-md-12">
            <div class="card mb-3">
              <div class="card-header">Order Summary</div>
              <div class="card-body">
                <table class="table">
                    <thead>
                        <tr>
                        <th scope="col">Product Image</th>
                        <th scope="col">SKU</th>
                        <th scope="col">Name</th>
                        <th scope="col">Price</th>
                        <th scope="col">Quantity</th>
                        <th scope="col">Total Price</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in order.order_details" :key="item.id">
                            <td><img :src="$helpers.getThumbUrl(item.avatar)" class="img-size-50" /></td>
                            <td>{{ item.sku }}</td>
                            <td>{{ (item.is_variant == 0?item.product_name:item.variant_name) }}</td>
                            <td>${{ item.price }}</td>
                            <td>{{ item.qty }}</td>
                            <td>${{ item.total_price }}</td>
                        </tr>
                    </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
       
        
        <div class="mb-4"></div>
      </div>
      <!-- End .col-lg-9 -->
      <aside class="sidebar col-lg-3">
        <DashboardSidebar />
      </aside>
      <!-- End .col-lg-3 -->
    </div>
    <!-- End .row -->
  </div>
  <!-- End .container -->
</template>
<script>
import DashboardSidebar from "../components/user/dashboardSidebar.vue";
import {
  USER_ORDER_DETAILS,
  ERROR_SOMETHING_WENT_WRONG,
} from "../config/constants";
import api from "../http-common";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "OrderDetails",
  props: ["id"],
  components: {
    DashboardSidebar,
  },
  data() {
    return {
      // order: {},
    };
  },
  computed: {
    ...mapGetters("order", ["order"]),
  },
  methods: {
    ...mapActions("order", ["saveOrderDetails"]),
    async getOrderDetails() {
      const uri = USER_ORDER_DETAILS.route + this.id;
      const response = await api.execute(USER_ORDER_DETAILS, uri);
      if (response && response.success == 1) {
        //this.order = response.data;
        this.saveOrderDetails(response.data);
        this.$toast.success(response.message);
      } else if (response && response.success == 0) {
        this.$toast.error(response.message);
      } else {
        this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
      }
    },
    orderDate(){
        const date = new Date(this.order.created_at);
        return date.toLocaleDateString();
    },
  },
  mounted() {
    this.getOrderDetails();
    console.log(this.order);
  },
};
</script>
<style scoped>
a.btn.btn-od {
  background-color: #ae1000 !important;
  border-color: #ae1000 !important;
  color: #fff;
  padding: 0.25rem 0.5rem;
  line-height: 1.5;
  border-radius: 0.2rem;
}
</style>
