<template>
    <section class="promo-section bg-dark" data-parallax="{'speed': 1.8, 'enableOnMobile': true}" data-image-src="assets/images/banners/banner-5.jpg" style="position: relative; overflow: hidden;">
        <div class="parallax-background" style="background-image: url(&quot;assets/images/banners/banner-5.jpg&quot;); background-size: cover; background-position: 50% 0%; position: absolute; top: 0px; left: 0px; width: 100%; height: 180%; transform: translate3d(0px, -139.498px, 0px);"></div>
        <div class="promo-banner banner container text-uppercase">
            <div class="banner-content row align-items-center text-center">
                <div class="col-md-4 ml-xl-auto text-md-right">
                    <h2 class="mb-md-0 text-white">{{ offer.offer_name }}<br>AVAILABLE</h2>
                </div>
                <div class="col-md-4 col-xl-3 pb-4 pb-md-0">
                    <a href="javascript:void(0)" class="btn btn-dark btn-black ls-10 c-primary-bg">View Sale</a>
                </div>
                <div class="col-md-4 mr-xl-auto text-md-left">
                    <h4 class="mb-1 coupon-sale-text p-0 d-block ls-10 text-transform-none"><b>VALID TILL: {{ $helpers.cDateFormat(offer.end_date) }}</b></h4>
                    <h5 class="mb-2 coupon-sale-text text-white ls-10 p-0">
                        <i class="ls-0">UP TO</i>
                        <b class="text-white c-primary-bg bg-secondary cd-primary-bg">${{maxDiscount}}</b> OFF
                    </h5>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
// import moment from 'moment'
export default {
    name: 'SellCTA',
    props:['offer'],
    data() {
        return {
            homeOffer: this.offer,
            maxDiscount: 0,
        }
    },
    created(){
        this.calculateMaxDiscount()
    },
    methods : {
        calculateMaxDiscount(){
            const offer = this.homeOffer;
            const discountArr = [];
            if(this.homeOffer.products){
                offer.products.forEach(item => {
                    discountArr.push(item.pivot.discount);
                });
                this.maxDiscount = Math.max(...discountArr);
            }else{
                console.log(this.maxDiscount);
            }
        }
    }
}
</script>