<template>
  <nav aria-label="breadcrumb" class="breadcrumb-nav">
    <div class="container">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link to="/"><i class="icon-home"></i></router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">Login</li>
      </ol>
    </div>
    <!-- End .container -->
  </nav>
  <div class="container">
    <div class="row">
      <div class="col-md-6">
        <div class="login-section">
          <div class="heading login-heading">
            <h2 class="title">Login</h2>
          </div>
          <!-- End .heading -->
          <form action="#" class="login-form" @submit.prevent="loginUser">
            <input
              type="email"
              class="form-control"
              placeholder="Email Address"
              required
              v-model="email"
            />
            <input
              type="password"
              class="form-control"
              placeholder="Password"
              required
              v-model="password"
            />

            <div class="form-footer">
              <button
                type="submit"
                class="btn btn-primary c-primary-bg"
                :disabled="btnDisabled ? true : false"
              >
                LOGIN
              </button>
              <a
                href="javascript:void(0)"
                class="forget-pass"
                @click="fpToggle = !fpToggle"
              >
                Forgot your password?</a
              >
            </div>
            <!-- End .form-footer -->
          </form>
        </div>
        <div class="forgot-password-section" v-show="fpToggle">
          <forgotPassword @custom-fp-toggle="updateFPToggle" />
        </div>
      </div>
      <!-- End .col-md-6 -->
      <div class="col-md-6">
        <div class="signup-section">
          <CreateAccount />
        </div>
      </div>
      <!-- End .col-md-6 -->
    </div>
    <!-- End .row -->
  </div>
  <!-- End .container -->

  <div class="mb-5"></div>
  <!-- margin -->
</template>
<script>
import { USER_SIGNIN, ERROR_SOMETHING_WENT_WRONG } from "../config/constants";
import api from "../http-common";
import CreateAccount from "../components/user/createAccount.vue";
import forgotPassword from "../components/user/forgotPassword.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      email: "",
      password: "",
      btnDisabled: false,
      fpToggle: false,
    };
  },
  components: {
    CreateAccount,
    forgotPassword,
  },
   mounted() {
    if (this.isLoggedIn) {
      this.$router.push({ name: "Dashboard" });
    }
  },
  methods: {
    ...mapActions("account", ["saveUser"]),
    ...mapGetters("account", ["isLoggedIn"]),
    async loginUser() {
      if (this.email != "" && this.password != "") {
        this.btnDisabled = true;
        let data = {
          email: this.email,
          password: this.password,
        };
        const response = await api.execute(
          USER_SIGNIN,
          USER_SIGNIN.route,
          data
        );
        if (response && response.success == 1) {
          this.saveUser(response.data);
          this.$toast.success(response.message);
          this.$router.push({ name: "Dashboard" });
          // this.$wkToast(response.message);
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
          this.btnDisabled = false;
        } else {
          this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
          this.btnDisabled = false;
        }
      } else {
        this.$toast.error("Email and Password Required");
      }
    },
    updateFPToggle(value) {
      this.fpToggle = value;
    },
  },
 
};
</script>
