/**
 * In the mutations.js of cart module, we will create One functions.
 * addToCart  save cart data
 */
export function addToCart(state, {vm,newItem}) {
    let found = -1;
    for (const cartIndex in state.cartItems) {
        const item = state.cartItems[cartIndex];
        // check item already exist or not
       if(item.product_id == newItem.product_id){
            if( newItem.is_variant == 1){
                if(item.variant_id == newItem.variant_id){
                    found = cartIndex;
                }
            }else{
                found = cartIndex;
            }
        }
    }
    if (found > -1) {
        if( state.cartItems[found].max_qty >= (newItem.qty + state.cartItems[found].qty)){
            state.cartItems[found].qty += newItem.qty || 1;
            vm.$toast.success(newItem.qty+" quantity added into cart");
        }else{
            vm.$toast.info(state.cartItems[found].max_qty+' quantity available in stock and you have already '+state.cartItems[found].qty+" quantity added");
        }
    } else {
        if (newItem.qty == undefined) {
            newItem.qty = 1;
        }
        state.itemCountBadge++; 
        state.cartItems.push(newItem);
        vm.$toast.success(newItem.qty+" quantity added into cart ");
    }
}
export function removeFromCart(state, index) {
    state.cartItems.splice(index, 1);
    state.itemCountBadge--;
}
export function emptyCart(state) {
    state.cartItems = [];
    state.itemCountBadge = 0;
}