export function itemCount (state) {
    return state.itemCountBadge;
}
export function cart (state) {
    return state.cartItems;
} 
export function cartTotal (state) {
    let subtotal = 0;
    for (const cartIndex in state.cartItems) {
        const item = state.cartItems[cartIndex];
        subtotal += (item.price * item.qty);
    }
    return subtotal;   
} 
export function itemKeys(state) {
    let results = [];
    for (const cartIndex in state.cartItems) {
        const item = state.cartItems[cartIndex];
        let keyRecord = {qty: item.qty};
        for (const keyIndex in state.cartKeys) {
            const keyValue = state.cartKeys[keyIndex];
            keyRecord[keyValue] = item[keyValue];
        }
        results.push(keyRecord);
    }
    return results;   
}
