/**
 * In the getters.js of the product module, we will create three functions:
 * products function  Returns the state that store the product list
 * product  Returns the state that store a specific product when the user wants to see the product details
 * cart  Returns the state that stores the products in the cart
 */
export function categories (state) {
    return state.categories
}
export function products (state) {
    return state.products
}
export function productDetails (state) {
    return state.product
}
export function featuredProducts (state) {
    return state.featured_products
}
export function latestProducts (state) {
    return state.latest_products
}
export function relatedProducts (state) {
    return state.related_products
}