/**
 * nside actions.js we will create a function to retrieve all products with Axios,
 *  a function to retrieve details of a product,
 *  a function to add the product to the cart and 
 *  a function to remove the product from the cart
 * 
 */
export function saveCategories({ commit },data) {
    commit("setCategories",data);
}

export function saveProducts({ commit },data) {
    commit("setProducts", data);
}
export function saveProductDetails({ commit }, data) {
    commit("setProduct", data);
}
export function saveFeaturedProducts({ commit },data) {
    commit("setFeaturedProducts", data);
}
export function saveLatestProducts({ commit },data) {
    commit("setLatestProducts", data);
}
export function saveRelatedProducts({ commit },data) {
    commit("setRelatedProducts", data);
}
