<template>
  <div class="heading login-heading">
    <h2 class="title">Create An Account</h2>
  </div>
  <form action="#" class="signup-form"  @submit.prevent="createAccount">
    <input
      type="text"
      class="form-control"
      minlength="3"
      maxlength="30"
      placeholder="Full Name *"
      required
      v-model="full_name"
    />
    <div class="row">
      <div class="col-sm-12 col-md-6">
        <input
          type="email"
          class="form-control"
          placeholder="Email Address *"
          minlength="6"
          maxlength="40"
          required
          v-model="email"
        />
      </div>
      <div class="col-sm-12 col-md-6">
        <input
          type="password"
          class="form-control"
          placeholder="Password *"
          minlength="6"
          maxlength="20"
          required
          v-model="password"
        />
      </div>
    </div>
    <div class="row">
      <div class="col-sm-2 col-md-2">
        <input
          type="text"
          class="form-control"
          minlength="6"
          maxlength="20"
          id="acc-phone-code"
          name="acc-phone-code"
          value="+1"
          readonly
        />
      </div>
      <div class="col-sm-10 col-md-10">
        <input
          type="text"
          class="form-control"
          placeholder="Phone Number *"
          minlength="6"
          maxlength="20"
          required
          v-model="phone"
        />
      </div>
    </div>

    <div class="form-footer">
      <button type="submit" class="btn btn-primary c-primary-bg" :disabled="isDisabled ? true : false">
        Create Account
      </button>
    </div>
    <!-- End .form-footer -->
  </form>
</template>

<script>
import api from "../../http-common";
import { USER_SIGNUP } from "../../config/constants";
import { mapActions } from "vuex";
export default {
  name: "CreateAccount",
  data() {
    return {
      full_name: "",
      email: "",
      password: "",
      confirm_password: "",
      phone: "",
      isDisabled: false,
    };
  },
  methods: {
    ...mapActions("account", ["saveUser"]),
    async createAccount() {
      if (this.full_name == "" && this.full_name.length < 3 || this.full_name.length > 30) {
        this.$toast.error("Please Enter Valid Name (min:3 and max:30 character)");
        return false;
      } else if (this.password == "" || this.password.length < 6 || this.password.length > 20) {
        this.$toast.error("Please Enter Valid Password (min:6 and max:20 character)");
      } else if (this.email == "" || this.email.length < 6 || this.password.length > 40) {
        this.$toast.error("Please Enter Valid Password (min:6 and max:20 character)");
      } else if (this.phone == "" || this.phone.length < 6 || this.password.length > 20) {
        this.$toast.error("Please Enter Valid Password (min:6 and max:20 character)");
      }else {
        this.isDisabled = true;
        let userData = {
          full_name: this.full_name,
          email: this.email,
          password: this.password,
          phone: this.phone,

        };
        const response = await api.execute(
          USER_SIGNUP,
          USER_SIGNUP.route,
          userData
        );
        if (response && response.success == 1) {
          this.$toast.success(response.message);
          this.saveUser(response.data);
          this.full_name = "";
          this.email = "";
          this.password = "";
          this.phone = "";
          this.$router.push({ name: "Dashboard" });
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
          this.isDisabled = false;
        } else {
          this.$toast.error("Something goes wrong");
          this.isDisabled = false;
        }
      }
    },
  },
};
</script>
<style scoped>
.login-heading {
  margin-bottom: 3rem;
}
.signup-form .form-control {
  height: 4rem;
  padding: 10px 10px;
}
.signup-form .btn {
  padding: 1.1rem 1.1rem;
}
.signup-form .form-footer button {
  font-size: 12px;
}
</style>
