<template>
  <div class="header-bottom sticky-header d-none d-lg-block">
    <div class="container">
      <nav class="main-nav w-100">
        <ul class="menu">
          <li>
            <router-link to="/" class="active">Home</router-link>
          </li>
          <li>
            <router-link to="/products">Products</router-link>
          </li>
          <li><router-link to="/offers">Offers</router-link></li>
          <li><router-link to="/coupons">Coupons</router-link></li>
          <li class="float-right mr-0">
            <a class="px-4 c-primary-color" @click="showModal = !showModal"
              >Watch Us! <span class="tip tip-new tip-top cd-primary-bg">Live</span></a
            >
            <!-- <a href="javascript:void(0)" class="px-4"  @click="show = !show">Watch Us! <span class="tip tip-new tip-top">Live</span></a> -->
          </li>
        </ul>
      </nav>
    </div>
    <!-- End .container -->
  </div>
  <!-- End .header-bottom -->
  <LiveVideoModal
    v-if="showModal"
    @close="showModal = false"
    :toggle="dp"
    :key="componentKey"
    :videosrc="livesrc"
  ></LiveVideoModal>
</template>

<script>
import { mapGetters } from "vuex";
import LiveVideoModal from "@/components/LiveVideoModal";
export default {
  name: "HeaderNav",
  components: { LiveVideoModal },
  computed: {
    ...mapGetters("youtube", ["getVideoLink"]),
  },
  data() {
    return {
      showModal: false,
      dp: "show",
      componentKey: 0,
      livesrc: "https://www.youtube.com/embed/LSkhIQWCMWk",
    };
  },
  watch: {
    showModal: function() {
      this.componentKey += 1;
      if (this.getVideoLink != "") {
        this.livesrc = this.getVideoLink;
      }
    },
  },
  methods: {
    toggleModal() {
      if (this.showModal == false) {
        this.showModal = true;
        this.dp = "show";
      } else {
        this.showModal = false;
        this.dp = "hide";
      }
    },
    forceRerender() {
      this.componentKey += 1;
    },
  },
};
</script>
<style scoped>
.modal-active {
  display: block;
}
a.c-primary-color{
    color: #ae1000;
    cursor: pointer;
}
a.c-primary-color:hover{
    color: #7b0b00;
}
.tip-new.tip-top:before {
    border-top-color: #7b0b00;
}
</style>
