<template>
  <form
    action="#"
    class="change-password-form"
    @submit.prevent="changePasswordChild"
  >
    <input
      type="password"
      class="form-control"
      placeholder="Enter Current Password"
      required
      v-model="old_password"
    />
    <input
      type="password"
      class="form-control"
      minlength="6"
      placeholder="Enter New Password"
      required
      v-model="password"
    />
    <input
      type="password"
      class="form-control"
      minlength="6"
      placeholder="Confirm New Password"
      required
      v-model="confirm_password"
    />

    <div class="form-footer">
      <button
        type="submit"
        class="btn btn-primary c-primary-bg"
        :disabled="btnDisabled ? true : false"
      >
        Change Password
      </button>
    </div>
    <!-- End .form-footer -->
  </form>
</template>
<script>
import api from "../../http-common";
import { USER_PASSWORD_CHANGE } from "../../config/constants";
export default {
  name: "ChangePasswordForm",
  data() {
    return {
      old_password: "",
      password: "",
      confirm_password: "",
      btnDisabled: false,
    };
  },
  methods: {
    async changePasswordChild() {
      if (this.old_password == "") {
        this.$toast.error("Please Enter Current Password");
        return false;
      } else if (this.password != this.confirm_password) {
        this.$toast.error("New Password and Confirm Password Not Match");
      } else {
        this.btnDisabled = true;
        let passData = {
          old_password: this.old_password,
          password: this.password,
          password_confirmation: this.confirm_password,
        };
        const response = await api.execute(
          USER_PASSWORD_CHANGE,
          USER_PASSWORD_CHANGE.route,
          passData
        );
        if (response && response.success == 1) {
          this.$toast.success(response.message);
          this.old_password = "";
          this.password = "";
          this.confirm_password = "";
          this.btnDisabled = true;
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
          this.btnDisabled = false;
        } else {
          this.$toast.error("Something goes wrong");
          this.btnDisabled = false;
        }
      }
    },
  },
};
</script>
<style scoped>
.login-heading {
  margin-bottom: 3rem;
}
.change-password-form .form-control {
  height: 4rem;
  padding: 10px 10px;
}
.change-password-form .btn {
  padding: 1.1rem 1.1rem;
}
.change-password-form .form-footer button {
  font-size: 12px;
}
</style>
