<template>
  <div class="heading forgot-heading">
    <h2 class="title">Forgot Password</h2>
    <p class="text-gray">Enter the email address you used to sign up & we will send you a password recovery email.</p>
  </div>
  <form
    action="#"
    class="forgot-password-form"
    @submit.prevent="forgotPassword"
  >
    <input
      type="email"
      class="form-control"
      placeholder="Email Address"
      required
      v-model="fp_email"
    />
    <div class="form-footer">
      <button
        type="submit"
        class="btn btn-primary c-primary-bg"
        :disabled="fpDisabled ? true : false"
      >
        Submit
      </button>
    </div>
    <!-- End .form-footer -->
  </form>
</template>
<script>
import {
  USER_FORGOT_PASSWORD,
  ERROR_SOMETHING_WENT_WRONG,
} from "../../config/constants";
import api from "../../http-common";
export default {
  name: "forgotPassword",
  inheritAttrs: false,
  data() {
    return {
      fp_email: "",
      fpDisabled: false,
    };
  },
  methods: {
    async forgotPassword() {
      if (this.fp_email != "") {
        this.fpDisabled = true;
        let data = {
          email: this.fp_email,
        };
        const response = await api.execute(
          USER_FORGOT_PASSWORD,
          USER_FORGOT_PASSWORD.route,
          data
        );
        if (response && response.success == 1) {
          this.$toast.success(response.message);
          this.$emit("custom-fp-toggle", false);
          this.fpDisabled = false;
          // this.$wkToast(response.message);
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
          this.fpDisabled = false;
        } else {
          this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
          this.fpDisabled = false;
        }
      } else {
        this.$toast.error("Email Required");
      }
    },
  },
};
</script>
