<template>
  <nav aria-label="breadcrumb" class="breadcrumb-nav">
    <div class="container">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link to="/"><i class="icon-home"></i></router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">Dashboard</li>
      </ol>
    </div>
    <!-- End .container -->
  </nav>

  <div class="container">
    <div class="row">
      <div class="col-lg-9 order-lg-last dashboard-content">
        <h2>My Dashboard</h2>
        <div class="alert alert-success" role="alert">
          Hello,
          <strong v-if="user.full_name">{{ user.full_name }}!</strong> From your
          My Account Dashboard you have the ability to view a snapshot of your
          recent account activity and update your account information. Select a
          link below to view or edit information.
        </div>
        <!-- End .alert -->

        <div class="mb-4"></div>
        <!-- margin -->

        <h3>Account Information</h3>

        <div class="row">
          <div class="col-md-8">
            <div class="card">
              <div class="card-header">
                Profile Details
                <a
                  href="javascript:void(0)"
                  class="card-edit"
                  @click="addressToggle = !addressToggle"
                  >Edit Profile</a
                >
              </div>
              <!-- End .card-header -->
              <div class="card-body">
                <div class="row">
                  <div class="col-md-6">
                    <address>
                      <strong>Full Name:</strong> {{ user.full_name }}<br />
                      <strong>Email:</strong> {{ user.email }}<br />
                      <strong>Phone:</strong> {{ user.phone_code }} {{ user.phone
                      }}<br />
                      <strong>Monster Points:</strong> {{ user.points }}<br />
                    </address>
                  </div>
                  <div class="col-md-6">
                    <address>
                      <strong>Full Address:</strong> {{ user.address }}<br />
                      <strong>State:</strong> {{ user.state }}<br />
                      <strong>City:</strong> {{ user.city }}<br />
                      <strong>Zipcode:</strong> {{ user.zipcode }}<br />
                    </address>
                  </div>
                </div>
                <div class="profile-address" v-show="addressToggle">
                  <div class="card-header">Update Information</div>
                  <ChangeAddressForm :userData="user" @custom-address-toggle="updateAddressToggle" />
                </div>
              </div>
              <!-- End .card-body -->
            </div>
            <!-- End .card -->
          </div>
          <!-- End .col-md-6 -->
          <div class="col-md-4">
            <div class="card">
              <div class="card-header">
                Change Password
              </div>
              <!-- End .card-header -->

              <div class="card-body">
                <div class="password-change">
                  <ChangePasswordForm />
                </div>
                <!-- End .card-body -->
              </div>
              <!-- End .card -->
            </div>
          </div>
        </div>
        <!-- End .row -->
      </div>
      <!-- End .col-lg-9 -->

      <aside class="sidebar col-lg-3">
        <DashboardSidebar />
      </aside>
      <!-- End .col-lg-3 -->
    </div>
    <!-- End .row -->
  </div>
  <!-- End .container -->
</template>
<script>
import { mapGetters } from "vuex";
import DashboardSidebar from "../components/user/dashboardSidebar.vue";
import ChangePasswordForm from "../components/user/changePasswordForm.vue";
import ChangeAddressForm from "../components/user/changeAddressForm.vue";
export default {
  name: "Dashboard",
  data() {
    return {
      addressToggle: false,
    };
  },
  components: {
    DashboardSidebar,
    ChangePasswordForm,
    ChangeAddressForm,
  },
  computed: {
    ...mapGetters("account", ["user", "isLoggedIn"]),
  },
  methods : {
    updateAddressToggle(value){
      console.log(value);
      this.addressToggle = value;
    }
  }
};
</script>
<style scoped>

.profile-address {
    margin: 20px 0px;
}
</style>
