<template>
  <nav aria-label="breadcrumb" class="breadcrumb-nav">
    <div class="container">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link to="/"><i class="icon-home"></i></router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">Wishlist</li>
      </ol>
    </div>
    <!-- End .container -->
  </nav>

  <div class="container">
    <div class="row">
      <div class="col-lg-9 order-lg-last dashboard-content">
        <div class="row">
          <div class="col-sm-12 col-md-12">
            <div class="card mb-3">
              <div class="card-header">My Wishlist</div>
              <div class="card-body">
                <table class="table">
                  <thead>
                    <tr>
                      <th scope="col">Product Image</th>
                      <th scope="col">Name</th>
                      <th scope="col">Price</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in products" :key="item.id">
                      <td>
                        <img
                          :src="$helpers.getThumbUrl(item.product_image)"
                          class="img-size-50"
                        />
                      </td>
                      <td>{{ item.product_name }}</td>
                      <td>{{
                        item.price_range == null
                          ? "$" + item.price
                          : item.price_range
                      }}</td>
                      <td>
                       <router-link :to="{ name: 'product', params: { slug: item.slug } }"  
                        class="btn btn-icon btn-sm" title="View Product">
                        <i class="fa fa-eye"></i>
                        </router-link> &nbsp; 
                         <a
                          href="javascript:void(0)"
                          @click="removeItemWishlist(item.id)"
                          class="btn btn-icon btn-sm"
                          title="Remove Product"
                          ><i class="fa fa-trash"></i
                        ></a> 
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
        <div class="mb-4"></div>
      </div>
      <!-- End .col-lg-9 -->
      <aside class="sidebar col-lg-3">
        <DashboardSidebar />
      </aside>
      <!-- End .col-lg-3 -->
    </div>
    <!-- End .row -->
  </div>
  <!-- End .container -->
</template>
<script>
import { mapGetters } from "vuex";
import DashboardSidebar from "../components/user/dashboardSidebar.vue";
import api from "../http-common";
import {
  USER_WISHLIST,
  ADD_REMOVE_WISHLIST,
  ERROR_SOMETHING_WENT_WRONG,
} from "../config/constants";
export default {
  name: "Wishlist",
  data() {
    return {
      products: {},
    };
  },
  components: {
    DashboardSidebar,
  },
  computed: {
    ...mapGetters("account", ["user", "isLoggedIn"]),
  },
  methods: {
    async getWishlist() {
      const response = await api.execute(USER_WISHLIST, USER_WISHLIST.route);
      if (response && response.success == 1) {
        this.products = response.data;
        this.$toast.success(response.message);
      } else if (response && response.success == 0) {
        this.$toast.error(response.message);
      } else {
        this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
      }
    },
    async removeItemWishlist(product_id){
      const payload = {
        product_id : product_id
      }
      const response = await api.execute(ADD_REMOVE_WISHLIST, ADD_REMOVE_WISHLIST.route,payload);
      if (response && response.success == 1) {
        this.updateWishListProduct(product_id);
        this.$toast.success(response.message);
      } else if (response && response.success == 0) {
        this.$toast.error(response.message);
      } else {
        this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
      }
    },
    updateWishListProduct(id){
      var index = this.products.map(x => {
        return x.id;
      }).indexOf(id);
      this.products.splice(index, 1);
    }
    
  },
  mounted(){
    this.getWishlist();
  }
};
</script>
