<template>
  <div class="about-page" id="about-content">
    <div
      class="page-header page-header-bg text-left"
      v-bind:style="{ background: '70%/cover #D4E1EA url(' + banne_image + ')' }"
    >
      <div class="container">
        <h1>
          <span>{{ item.title }}</span
          >BREAK RULES
        </h1>
        <router-link to="/contact" class="btn btn-dark">Contact</router-link>
      </div>
      <!-- End .container -->
    </div>
    <!-- End .page-header -->
    <nav aria-label="breadcrumb" class="breadcrumb-nav">
      <div class="container">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link to="/"><i class="icon-home"></i> Home </router-link>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{ item.title }}
          </li>
        </ol>
      </div>
      <!-- End .container -->
    </nav>
    <div class="about-section">
      <div class="container">
        <div v-if="isShow" v-html="content"></div>
      </div>
      <!-- End .container -->
    </div>
    <!-- End .about-section -->
    <div class="features-section bg-gray">
      <div class="container">
        <h2 class="subtitle">WHY CHOOSE US</h2>
        <div class="row">
          <div class="col-lg-4">
            <div class="feature-box bg-white">
              <i class="icon-shipped"></i>

              <div class="feature-box-content">
                <h3>Free Shipping</h3>
                <p>
                  Lorem Ipsum is simply dummy text of the printing and
                  typesetting industry. Lorem Ipsum has been the industr in some
                  form.
                </p>
              </div>
              <!-- End .feature-box-content -->
            </div>
            <!-- End .feature-box -->
          </div>
          <!-- End .col-lg-4 -->

          <div class="col-lg-4">
            <div class="feature-box bg-white">
              <i class="icon-us-dollar"></i>

              <div class="feature-box-content">
                <h3>100% Money Back Guarantee</h3>
                <p>
                  There are many variations of passages of Lorem Ipsum
                  available, but the majority have suffered alteration in some
                  form.
                </p>
              </div>
              <!-- End .feature-box-content -->
            </div>
            <!-- End .feature-box -->
          </div>
          <!-- End .col-lg-4 -->

          <div class="col-lg-4">
            <div class="feature-box bg-white">
              <i class="icon-online-support"></i>

              <div class="feature-box-content">
                <h3>Online Support 24/7</h3>
                <p>
                  There are many variations of passages of Lorem Ipsum
                  available, but the majority have suffered alteration in some
                  form.
                </p>
              </div>
              <!-- End .feature-box-content -->
            </div>
            <!-- End .feature-box -->
          </div>
          <!-- End .col-lg-4 -->
        </div>
        <!-- End .row -->
      </div>
      <!-- End .container -->
    </div>
    <!-- End .features-section -->
  </div>
</template>
<script>
import { PAGE_DATA } from "../config/constants";
import api from "../http-common";

// direct instance creation
export default {
  // el: "about-content",
  name: "Page",
  props: ["slug"],
  data() {
    return {
      item: [],
      isShow: false,
      product: "hhh",
      content: "<h1>Page content </h1>",
      page_slug: this.$route.params.slug,
      banne_image: "",
    };
  },
  watch: {
    $route() {
      // this will be called any time the route changes
      this.getPageData();
    },
  },
  methods: {
    async getPageData() {
      try {
        let url = PAGE_DATA.route + this.$route.params.slug;
        const results = await api.execute(PAGE_DATA, url);
        this.item = results.data;
        this.content = this.item.content;
        this.banne_image = api.getImageUrl(this.item.banner_image);
        this.isShow = true;
      } catch (err) {
        if (err.response) {
          // client received an error response (5xx, 4xx)
          console.log("Server Error:", err);
        } else if (err.request) {
          // client never received a response, or request never left
          console.log("Network Error:", err);
        } else {
          console.log("Client Error:", err);
        }
      }
    },
  },
  mounted() {
    this.getPageData();
  },
};
</script>
