<template>
  <nav aria-label="breadcrumb" class="breadcrumb-nav">
    <div class="container">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <router-link to="/"><i class="icon-home"></i></router-link>
        </li>
        <li class="breadcrumb-item active" aria-current="page">Wishlist</li>
      </ol>
    </div>
    <!-- End .container -->
  </nav>

  <div class="container">
    <div class="row">
      <div class="col-lg-9 order-lg-last dashboard-content">
        <div class="row">
          <div class="col-sm-12 col-md-12">
            <div class="card mb-3">
              <div class="card-header">My Orders</div>
              <div class="card-body">
                <table class="table">
                  <thead>
                    <tr>
                      <th scope="col">Order#</th>
                      <th scope="col">Total Amount</th>
                      <th scope="col">Discount</th>
                      <th scope="col">Order Status</th>
                      <th scope="col">Payment Status</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody v-if="orders.length > 0">
                    <tr v-for="order in orders" :key="order.id">
                      <th scope="row">{{ order.order_number }}</th>
                      <td>${{ order.total_amount }}</td>
                      <td>${{ order.discount }}</td>
                      <td>{{ order.order_status }}</td>
                      <td>{{ order.payment_status }}</td>
                      <td>
                        <router-link
                          :to="`/user/order/${order.id}`"
                          class="btn btn-icon btn-sm"
                          title="View Order Details"
                          ><i class="fa fa-eye"></i
                        ></router-link>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
        <div class="mb-4"></div>
      </div>
      <!-- End .col-lg-9 -->
      <aside class="sidebar col-lg-3">
        <DashboardSidebar />
      </aside>
      <!-- End .col-lg-3 -->
    </div>
    <!-- End .row -->
  </div>
  <!-- End .container -->
</template>
<script>
import DashboardSidebar from "../components/user/dashboardSidebar.vue";
import { USER_ORDERS, ERROR_SOMETHING_WENT_WRONG } from "../config/constants";
import api from "../http-common";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Order",
  components: {
    DashboardSidebar,
  },
  data() {
    return {
      // orders: [],
    };
  },
  computed: {
    ...mapGetters("order", ["orders"]),
  },
  methods: {
    ...mapActions("order", ["saveAllOrders"]),
    async getOrders() {
      const response = await api.execute(USER_ORDERS, USER_ORDERS.route);
      if (response && response.success == 1) {
        //this.orders = response.data;
        this.saveAllOrders(response.data);
        this.$toast.success(response.message);
      } else if (response && response.success == 0) {
        this.$toast.error(response.message);
      } else {
        this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
      }
    },
  },
  mounted() {
    this.getOrders();
  },
};
</script>
