<template>
  <div class="header-middle">
    <div class="container">
      <div class="header-left col-lg-2 w-auto pl-0">
        <button class="mobile-menu-toggler text-primary mr-2" type="button">
          <i class="icon-menu"></i>
        </button>
        <router-link to="/" class="logo"
          ><img src="/assets/images/logo.png" alt="Porto Logo"
        /></router-link>
      </div>
      <!-- End .header-left -->

      <div class="header-right w-lg-max">
        <HeaderSearchForm ></HeaderSearchForm>

        <div class="header-contact d-none d-lg-flex pl-4 ml-3 mr-xl-5 pr-4">
          <img
            alt="phone"
            src="/assets/images/phone.png"
            width="30"
            height="30"
            class="pb-1"
          />
          <h6>
            Call us now<a href="tel:#" class="text-dark font1">+123 5678 890</a>
          </h6>
        </div>
        <router-link to="/user/login" class="header-icon"
          ><i class="icon-user-2"></i
        ></router-link>
        <!-- <a href="#" class="header-icon"><i class="icon-wishlist-2"></i></a> -->

        <div class="dropdown cart-dropdown">
           <router-link to="/cart" 
             class="dropdown-toggle dropdown-arrow"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
              data-display="static"
           >
            <i class="icon-shopping-cart"></i>
            <span class="cart-count badge-circle">{{ itemCount }}</span>
            </router-link>
          <!-- <a
            href="javascript:void(0)"
            class="dropdown-toggle dropdown-arrow"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
            data-display="static"

          >
            <i class="icon-shopping-cart"></i>
            <span class="cart-count badge-circle">{{ itemCount }}</span>
          </a> -->

          <div class="dropdown-menu" v-if="itemCount > 0">
            <div class="dropdownmenu-wrapper">
              <div class="dropdown-cart-header">
                <span>{{ itemCount }} Items</span>
                <router-link to="/cart" class="float-right">View Cart</router-link>
              </div>
              <!-- End .dropdown-cart-header -->

              <div class="dropdown-cart-products">
                <div class="product" v-for="(item, index) in cart" :key="index">
                  <div class="product-details">
                    <h4 class="product-title">
                      <a href="product.html">{{ (item.is_variant == 1?item.variant_name:item.product_name) }}</a>
                    </h4>
                    <span class="cart-product-info">
                      <span class="cart-product-qty">{{ item.qty }}</span>
                      x ${{ item.price }}
                    </span>
                  </div>
                  <!-- End .product-details -->
                  <figure class="product-image-container">
                    <a href="product.html" class="product-image">
                      <img
                        :src="$helpers.getThumbUrl(item.avatar)"
                        alt="product"
                        width="80"
                        height="80"
                      />
                    </a>
                    <a
                      href="javascript:void(0)"
                      @click="removeMiniCartItem(index)"
                      class="btn-remove icon-cancel"
                      title="Remove Product"
                    ></a>
                  </figure>
                </div>
                <!-- End .product -->
              </div>
              <!-- End .cart-product -->

              <div class="dropdown-cart-total">
                <span>Total</span>
                <span class="cart-total-price float-right">${{ cartTotal.toFixed(2) }}</span>
              </div>
              <!-- End .dropdown-cart-total -->

              <div class="dropdown-cart-action">
                <a href="checkout-shipping.html" class="btn btn-dark btn-block"
                  >Checkout</a
                >
              </div>
              <!-- End .dropdown-cart-total -->
            </div>
            <!-- End .dropdownmenu-wrapper -->
          </div>
          <!-- End .dropdown-menu -->
        </div>
        <!-- End .dropdown -->
      </div>
      <!-- End .header-right -->
    </div>
    <!-- End .container -->
  </div>
  <!-- End .header-middle -->
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import HeaderSearchForm from '@/components/header/HeaderSearchForm'
export default {
  name: "HeaderMid",
  components : { HeaderSearchForm },
  data() {
    return {
      componentKey: 0,
      subTotal : 0
    };
  },
  computed: {
    ...mapGetters("cart", ["cart","itemCount","cartTotal"])
  },
  methods : {
    ...mapActions("cart", ["removeItem"]),
    removeMiniCartItem(index){
        this.removeItem(index);
    }
  }
};
</script>
<style scoped>
.dropdown-cart-products {
    max-height: 230px;
    overflow-y: scroll;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
/* .dropdown-cart-products::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
} */
 
/* Handle */
.dropdown-cart-products::-webkit-scrollbar-thumb {
  background: #ae1000; 
  border-radius: 10px;
}

/* Handle on hover */
.dropdown-cart-products::-webkit-scrollbar-thumb:hover {
  background: #7b0b00; 
}
</style>