<template>
  <div class="product-detail-page" id="product-detail-page">
    <div class="container">
      <nav aria-label="breadcrumb" class="breadcrumb-nav">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link to="/"><i class="icon-home"></i></router-link>
          </li>
          <li class="breadcrumb-item">
            <a href="#">{{ product_slug }}</a>
          </li>
        </ol>
      </nav>
      <div
        class="product-single-container product-single-default"
        v-if="slides.length > 0"
      >
        <div class="row">
          <div class="col-md-5 product-single-gallery">
            <agile  
               ref="list"
              :nav-buttons="false"
              :autoplay-speed="5000"
              :speed="2500"
              fade="fade"
              pause-on-hover="pause-on-hover"
              pause-on-dots-hover="pause-on-dots-hover"
              autoplay="autoplay"
            >
              <img
                class="slide"
                v-for="(slide, index) in slides"
                :key="index"
                :src="$helpers.getImageUrl(slide)"
              />
            </agile>
          </div>
          <!-- End .product-single-gallery -->

          <div class="col-md-7 product-single-details">
            <h1 class="product-title">{{ product.product_name }}</h1>
            <hr class="short-divider" />
            <div class="product-desc">
              <p>
                <span v-if="!readMoreActivated">{{
                  product.product_description.slice(0, 220)
                }}</span>
                <a class="read-more"
                  href="javascript:void(0)"
                  v-if="!readMoreActivated && showReadMore"
                  @click="activateReadMore"
                >
                  ( ...read more)</a
                >
                <span v-if="readMoreActivated">{{
                  product.product_description
                }}</span>
                <a class="read-less"
                  href="javascript:void(0)"
                  v-if="readMoreActivated"
                  @click="activateReadLess"
                >
                  (read less)</a
                >
              </p>
            </div>
            <div class="product-filters-container">
              <div class="product-single-filter mb-2">
                <label
                  >Category:
                  <strong
                    ><em>{{ product.category.category_name }}</em></strong
                  ></label
                >
                <label
                  >Product SKU:
                  <strong
                    ><em>{{ sku }}</em></strong
                  ></label
                >
              </div>
              <!-- End .product-single-filter -->
            </div>
            <!-- End .product-desc -->
            <hr class="divider" />
            <div class="price-box">
              <span class="product-price">PRICE: {{ price_text }}</span>
            </div>
            <div class="product-action">
              <div
                v-if="product.is_variant == 1"
                class="product-variant-select"
              >
                <label>Variants:</label>
                <select
                  name=""
                  id=""
                  class=" c-form-control"
                  v-model="variant_id"
                >
                  <option value="0" selected>Select Option</option>
                  <option
                    v-for="(variant, index) in product.variants"
                    :key="index"
                    :value="variant.id"
                    >{{ variant.variant_name }}</option
                  >
                </select>
              </div>
              <div class="product-single-qty" v-if="isShow">
                <input
                  class="horizontal-quantity c-form-control"
                  type="number"
                  :max="maxQty"
                  v-model="qty"
                />
              </div>
              <div class="product-add-to-cart" v-if="isShow">
                <a
                  href="javascript:void(0)"
                  class="btn btn-dark add-cart icon-shopping-cart"
                  title="Add to Cart"
                  @click="addToCart"
                  >Add to Cart</a
                >
              </div>
            </div>
            <!-- End .product-action -->
            <hr class="divider mb-1" />
          </div>
          <!-- End .product-single-details -->
        </div>
        <!-- End .row -->
      </div>
      <!-- End .product-single-container -->

      <div class="product-single-tabs" v-if="product">
        <ul class="nav nav-tabs" role="tablist">
          <li class="nav-item">
            <a
              class="nav-link active"
              id="product-tab-desc"
              data-toggle="tab"
              href="#product-desc-content"
              role="tab"
              aria-controls="product-desc-content"
              aria-selected="true"
              >Description</a
            >
          </li>
        </ul>
        <div class="tab-content">
          <div
            class="tab-pane fade show active"
            id="product-desc-content"
            role="tabpanel"
            aria-labelledby="product-tab-desc"
          >
            <div class="product-desc-content">
              <p>{{ product.product_description }}</p>
            </div>
            <!-- End .product-desc-content -->
          </div>
          <!-- End .tab-pane -->
        </div>
        <!-- End .tab-content -->
      </div>
      <!-- End .product-single-tabs -->

      <div class="products-section pt-0 custom-products" id="v-carousel" v-if="relatedProducts.length">
        <h2 class="section-title">Related Products</h2>
        <RelatedProductSlider :key="componentKey" />
      </div>
      <!-- End .products-section -->
    </div>
    <!-- End .container -->
  </div>
</template>
<script>
import {
  PRODUCT_DETAILS,
  RELETED_PRODUCT,
  ERROR_SOMETHING_WENT_WRONG,
} from "../config/constants";
import api from "../http-common";
import RelatedProductSlider from "../components/product/RelatedProductSlider.vue";
import { mapActions, mapGetters } from "vuex";
import { VueAgile } from "vue-agile";
// direct instance creation
export default {
  name: "ProductDetails",
  components: {
    RelatedProductSlider,
    agile: VueAgile,
  },
  props: ["slug"],
  
  data() {
    return {
      slides: [],
      variants: [],
      isShow: false,
      showReadMore: false,
      readMoreActivated: false,
      product: this.productDetails,
      related_products: [],
      price_text: "",
      componentKey: 0,
      sku: "",
      product_slug: this.$route.params.slug,
      maxQty: 0,
      variant_id: 0,
      variant_name: null,
      price:0,
      qty: 1,
      avatar: "",
      cartItem:{}
    };
  },
  watch: {
    qty: function(val) {
      if (val > this.maxQty) {
        this.qty = this.maxQty;
        this.$toast.info("The Maximum Qty Available: " + this.maxQty);
      }else if(val <= 0){
        this.qty = 1;
      }
    },
    slug: function() {
      this.variant_id = 0;
      if(this.is_variant){
        this.isShow = false;
      }
      this.slides = [];
      this.getProductDetails();
      this.$refs.list.reload();
      
    },
    variant_id: function(val) {
      this.qty = 1;
      if (val == 0) {
        this.isShow = false;
        this.price_text = this.product.price_range;
        this.sku = "";
      } else {
        this.isShow = true;
        let index = this.product.variants.findIndex(x => x.id ===val);
        this.variant_name = this.product.variants[index].variant_name;
        this.price = this.product.variants[index].price;
        this.price_text = "$"+this.price;
        this.sku = this.product.variants[index].sku;
        this.maxQty = this.product.variants[index].qty;
        if(this.qty > this.maxQty){
          this.qty = this.maxQty;
        }
      }
    },
  },
  computed: {
    ...mapGetters("product", ["productDetails","relatedProducts"]),
    ...mapGetters("account", ["getUserId"]),
    ...mapGetters("cart", ["cart","itemCount"]),
  },
  methods: {
    ...mapActions("product", ["saveProductDetails","saveRelatedProducts"]),
    ...mapActions("cart", ["addItem"]),
    async getProductDetails() {
      try {
        let myRoute = PRODUCT_DETAILS.route + this.$route.params.slug;
        if (this.getUserId != null) {
          myRoute += "?user_id=" + this.getUserId;
        }
        const response = await api.execute(PRODUCT_DETAILS, myRoute);
        if (response && response.success == 1) {
          this.saveProductDetails(response.data);
          this.saveRelatedProducts(response.data.related_products);
          this.product = response.data;
          this.$toast.success(response.message);
          
          this.is_wishlist = this.product.is_wishlist_product;
          this.maxQty = this.product.qty;
          this.qty = this.product.qty > 0 ? 1 : 0;
          this.showReadMore =  (this.product.product_description.length > 220?true:false);
          if (this.product.is_variant == 1) {
            this.price_text = this.product.price_range;
            this.isVariantShow = true;
          } else {
            this.price_text = "$" + this.product.price;
            this.price = this.product.price;
            this.isShow = true;
          }
          this.slides.push(this.product.product_image);
          if (this.product.gallery.length > 0) {
            this.product.gallery.forEach((item) => {
              this.slides.push(item.product_image);
            });
          }
          this.sku = this.product.sku;
          this.componentKey++;
          //this.getRelatedProducts();
        } else if (response && response.success == 0) {
          this.$toast.error(response.message);
          //this.$router.push({ name: "/" });
        } else {
          this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
          //this.$router.push({ name: "/" });
        }
      } catch (err) {
        if (err.response) {
          // client received an error response (5xx, 4xx)
          console.log("Server Error:", err);
        } else if (err.request) {
          // client never received a response, or request never left
          console.log("Network Error:", err);
        } else {
          console.log("Client Error:", err);
        }
      }
    },
    async getRelatedProducts(){
       try {
        let myRoute = RELETED_PRODUCT.route + "?category_id="+ this.product.category_id+ "&product_id="+this.product.id;
        const response = await api.execute(RELETED_PRODUCT, myRoute);
        if (response && response.success == 1) {
          this.$toast.success(response.message);
          // this.isShow = true;
          this.related_products = response.data;
          this.componentKey++;
        } else if (response && response.success == 0) {
          alert();
          this.$toast.error(response.message);
          //this.$router.push({ name: "/" });
        } else {
          this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
          //this.$router.push({ name: "/" });
        }
      } catch (err) {
        if (err.response) {
          // client received an error response (5xx, 4xx)
          console.log("Server Error:", err);
        } else if (err.request) {
          // client never received a response, or request never left
          console.log("Network Error:", err);
        } else {
          console.log("Client Error:", err);
        }
      }
    },
    activateReadMore() {
      this.readMoreActivated = true;
    },
    activateReadLess() {
      this.readMoreActivated = false;
    },
    addToCart(){
      let item = {
        product_id : this.product.id,
        product_name : this.product.product_name,
        avatar : this.product.product_image,
        price : parseFloat(this.price),
        qty : parseInt(this.qty),
        is_variant: this.product.is_variant,
        variant_id : this.variant_id,
        variant_name : this.variant_name,
        max_qty : parseInt(this.maxQty)
      }
      this.addItem({vm : this,cartItem : item});
    }
  },
  mounted() {
    this.getProductDetails();
    window.scrollTo(0, 0);
  },
};
</script>
<style scoped>
select.c-form-control,
input.c-form-control {
  height: 4.5rem;
  width: 100%;
  margin-bottom: 1rem;
  padding: 1rem;
  transition: all 0.3s;
  border: 1px solid #dfdfdf;
  border-radius: 0;
  background-color: #fff;
  color: #777;
  font-family: "Open Sans", sans-serif;
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.5;
}
.product-variant-select {
  width: 50%;
}
.product-single-qty {
  max-width: 20%;
}
.product-add-to-cart {
  display: inline-block;
  width: 30%;
}

.product-add-to-cart a.add-cart {
  display: block;
  height: 4.5rem;
  padding: 12px 28px;
}
.config-size-list li a {
  width: auto;
}
.product-single-details .product-desc a.read-more,.product-single-details .product-desc a.read-less{
  color: #979696;
  border-bottom: none;
  font-size: 12px;
}
.product-single-gallery {
    margin: 0 auto;
    display: block;
    height: 450px;
    text-align: center;
    overflow: hidden;
    border: 1px solid #e7e7e7;
}
.product-single-gallery img{
  height: 445px;
}
</style>
