/**
 * In the mutations.js of product module, we will create four functions.
 * setProducts  Assign product list to state products
 * setProduct  Assign an object with a specific product to the product state
 * setCart  Assigns a list of added products in the cart to cart state
 */
export function setCategories(state,val){
    state.categories = val;
}
export function setProducts(state, val) {
    state.products = val
}
export function setProduct(state, val) {
    let singleData = {
        id : val.id,
        category_id : val.category_id,
        product_name : val.product_name,
        slug : val.slug,
        sku : val.sku,
        product_description : val.product_description,
        product_image : val.product_image,
        price : val.price,
        price_range : val.price_range,
        qty : val.qty,
        sold_count : val.sold_count,
        no_views : val.no_views,
        is_featured : val.is_featured,
        is_variant : val.is_variant,
        is_active : val.is_active,
        is_wishlist_product : val.is_wishlist_product,
        updated_at : val.updated_at,
        category : val.category,
        variants : val.variants,
        gallery : val.gallery,

    };
    state.product = singleData
}
export function setLoad(state, val) {
    state.uploadingData = val
}
export function setFeaturedProducts(state, val) {
    state.featured_products = val
}
export function setLatestProducts(state, val) {
    state.latest_products = val
}
export function setRelatedProducts(state, val) {
    state.related_products = val
}