<template>
  <div id="cart-page">
    <nav aria-label="breadcrumb" class="breadcrumb-nav">
      <div class="container">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link to="/"><i class="icon-home"></i></router-link>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            Shopping Cart
          </li>
        </ol>
      </div>
      <!-- End .container -->
    </nav>
    <div class="container">
      <div class="row" v-if="cart.length > 0">
        <div class="col-lg-8">
          <div class="cart-table-container">
            <table class="table table-cart">
              <thead>
                <tr>
                  <th class="product-col">Product</th>
                  <th class="price-col">Price</th>
                  <th class="qty-col">Qty</th>
                  <th>Subtotal</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  class="product-row"
                  v-for="(item, index) in cart"
                  :key="index"
                >
                  <td class="product-col">
                    <figure class="product-image-container">
                      <a href="product.html" class="product-image">
                        <img
                          :src="$helpers.getThumbUrl(item.avatar)"
                          alt="product"
                        />
                      </a>
                    </figure>
                    <h2 class="product-title">
                      <a href="javascript:void(0)">{{
                        item.is_variant == 1
                          ? item.variant_name
                          : item.product_name
                      }}</a>
                    </h2>
                  </td>
                  <td>${{ parseFloat(item.price).toFixed(2) }}</td>
                  <td>
                    <input
                      :id="`qty-input-` + index"
                      class="c-form-control small-qty input-number"
                      type="number"
                      :value="item.qty"
                      :min="1"
                      :max="item.max_qty"
                      @change="changeQty(index)"
                    />
                  </td>
                  <td>${{ parseFloat(item.price * item.qty).toFixed(2) }}</td>
                  <td>
                    <div class="float-right">
                      <a
                        href="javascript:void(0)"
                        @click="removeCartItem(index)"
                        title="Remove product"
                        class="btn-remove"
                        ><span class="sr-only">Remove</span
                        ><i class="fas fa-times-circle text-danger"></i
                      ></a>
                    </div>
                    <!-- End .float-right -->
                  </td>
                </tr>
              </tbody>

              <tfoot>
                <tr>
                  <td colspan="5" class="clearfix">
                    <div class="float-left">
                      <router-link to="/" class="btn btn-outline-secondary"
                        >Continue Shopping</router-link
                      >
                    </div>
                    <!-- End .float-left -->

                    <div class="float-right">
                        
                      <a
                        href="javascript:void(0)"
                        @click="emptyItems()"
                        class="btn btn-outline-secondary btn-clear-cart"
                        >Clear Shopping Cart</a
                      >
                    </div>
                    <!-- End .float-right -->
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
          <!-- End .cart-table-container -->
          <!-- <div class="cart-discount">
            <h4>Apply Discount Code</h4>
            <form action="#">
              <div class="input-group">
                <input
                  type="text"
                  class="form-control form-control-sm"
                  placeholder="Enter discount code"
                  required
                />
                <div class="input-group-append">
                  <button class="btn btn-sm btn-primary" type="submit">
                    Apply Discount
                  </button>
                </div>
              </div>
            </form>
          </div> -->
          <!-- End .cart-discount -->
        </div>
        <!-- End .col-lg-8 -->
        <div class="col-lg-4">
          <div class="cart-summary">
            <h3>Summary</h3>
            <h4>
              <a
                style="display:none"
                data-toggle="collapse"
                href="#total-estimate-section"
                class="collapsed"
                role="button"
                aria-expanded="false"
                aria-controls="total-estimate-section"
                >Estimate Shipping and Tax</a
              >
            </h4>

            <div class="collapse" id="total-estimate-section">
              <form action="#">
                <div class="form-group form-group-sm">
                  <label>Country</label>
                  <div class="select-custom">
                    <select class="form-control form-control-sm">
                      <option value="USA">United States</option>
                      <option value="Turkey">Turkey</option>
                      <option value="China">China</option>
                      <option value="Germany">Germany</option>
                    </select>
                  </div>
                  <!-- End .select-custom -->
                </div>
                <!-- End .form-group -->

                <div class="form-group form-group-sm">
                  <label>State/Province</label>
                  <div class="select-custom">
                    <select class="form-control form-control-sm">
                      <option value="CA">California</option>
                      <option value="TX">Texas</option>
                    </select>
                  </div>
                  <!-- End .select-custom -->
                </div>
                <!-- End .form-group -->

                <div class="form-group form-group-sm">
                  <label>Zip/Postal Code</label>
                  <input type="text" class="form-control form-control-sm" />
                </div>
                <!-- End .form-group -->

                <div class="form-group form-group-custom-control">
                  <label>Flat Way</label>
                  <div class="custom-control custom-checkbox">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="flat-rate"
                    />
                    <label class="custom-control-label" for="flat-rate"
                      >Fixed $5.00</label
                    >
                  </div>
                  <!-- End .custom-checkbox -->
                </div>
                <!-- End .form-group -->

                <div class="form-group form-group-custom-control">
                  <label>Best Rate</label>
                  <div class="custom-control custom-checkbox">
                    <input
                      type="checkbox"
                      class="custom-control-input"
                      id="best-rate"
                    />
                    <label class="custom-control-label" for="best-rate"
                      >Table Rate $15.00</label
                    >
                  </div>
                  <!-- End .custom-checkbox -->
                </div>
                <!-- End .form-group -->
              </form>
            </div>
            <!-- End #total-estimate-section -->

            <table class="table table-totals">
              <tbody>
                <tr>
                  <td>Subtotal</td>
                  <td>${{ parseFloat(cartTotal).toFixed(2) }}</td>
                </tr>

                <tr>
                  <td>Tax</td>
                  <td>${{ parseFloat(tax).toFixed(2) }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <td>Order Total</td>
                  <td>${{ getOrderTotal }}</td>
                </tr>
              </tfoot>
            </table>

            <div class="checkout-methods">
              <a
                href="checkout-shipping.html"
                class="btn btn-block btn-sm btn-primary"
                >Go to Checkout</a
              >
              <p class="mt-2 text-center"><strong>Price excludes delivery, which is applied at checkout</strong></p>
               
            </div>
            <!-- End .checkout-methods -->
          </div>
          <!-- End .cart-summary -->
        </div>
        <!-- End .col-lg-4 -->
      </div>
      <!-- End .row -->
      <div class="row" v-else>
        <div class="col-sm-12">
          <div class="text-center">
            <div class="cart-img-div">
              <img src="/assets/images/cart-icon.png" alt="No Item Found" />
            </div>
            <h2 class="text-center">No Item Found In Your Cart</h2>
            <router-link to="/" class="btn btn-sm cd-primary-bg"
              >Make Some Shopping</router-link
            >
          </div>
        </div>
      </div>
    </div>
    <!-- End .container -->
    <div class="mb-6"></div>
    <!-- margin -->
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Cart",
  data() {
    return {
      componentKey: 0,
      subTotal: 0,
      tax: 0,
      orderTotal: 0,
    };
  },
  computed: {
    ...mapGetters("cart", ["cart", "itemCount", "cartTotal"]),
    getOrderTotal() {
      return parseFloat(this.cartTotal + this.tax).toFixed(2);
    },
  },
  methods: {
    ...mapActions("cart", ["removeItem","emptyItems"]),
    removeCartItem(index) {
      this.removeItem(index);
    },
    changeQty: function(index) {
      let qtyInput = document.getElementById("qty-input-" + index);
      let maxQ = parseInt(qtyInput.getAttribute("max"));
      let val = parseInt(qtyInput.value);
      if(maxQ <= 0){
          this.removeItem(index);
      }else{
        if (maxQ >= val) {
            if (val <= 0) {
            qtyInput.value = 1;
            val = 1;
            }
            this.updateQty(index, val);
        } else {
            qtyInput.value = maxQ;
            this.$toast.info("We have only " + maxQ + " items in stock");
        }
      }
    },
    updateQty: function(index, qty) {
      let cartItem = this.cart[index];
      if (cartItem.max_qty >= qty) {
        cartItem.qty = qty;
        // this.$toast.success(cartItem.product_name + " quantity updated");
      } else {
        this.$toast.info("We have only " + cartItem.max_qty + " item in stock");
      }
      //this.$store.dispatch('updateCart', {vm : this, cartItem : cartItem});
    },
  },
};
</script>
<style scoped>
#cart-page .cart-img-div {
  width: 150px;
  height: 150px;
  margin: 0 auto;
}
#cart-page input.c-form-control.small-qty {
  max-width: 70px;
}
#cart-page td.product-col {
  font-size: 12px;
}
#cart-page figure.product-image-container {
  width: 100px;
  height: 100px;
}
#cart-page figure.product-image-container img {
  width: 100%;
}
#cart-page h2.product-title {
  font-size: 18px;
}
</style>
