<template>
  <div id="home-page">
    <SliderHome />
    <div class="container">
      <FreeShippingSlider />
    </div>
    <!-- End .container -->
    <FeaturedProductSlider :key="componentKey" />
    <section class="new-products-section">
      <div class="container">
        <h2 class="section-title heading-border ls-20 border-0">
          New Arrivals
        </h2>
        <div id="v-carousel" class="custom-products">
          <LatestProductSlider :key="componentKey" />
        </div>
      </div>
    </section>
    <SellCTA :offer="homeOffer" :key="componentKey" v-show="is_offer"/>
    <section class="new-products-section">
      <div class="container">
        <h2 class="section-title heading-border border-0 mb-5">
          Browse Categories
        </h2>
        <div id="v-carousel" class="custom-products">
          <CategorySlider :key="componentKey" />
        </div>
      </div>
    </section>
</div>
</template>
<script>
import SliderHome from "../components/slider/SliderHome.vue";
import FreeShippingSlider from "../components/slider/FreeShippingSlider";
import FeaturedProductSlider from "../components/product/FeaturedProductSlider";
// import carousel from '../components/slider/OwlSlider'
import LatestProductSlider from "../components/product/LatestProductSlider.vue";
import SellCTA from "../components/product/SellCTA";
import CategorySlider from "../components/product/CategorySlider";
import { GET_DASHBOARD, ERROR_SOMETHING_WENT_WRONG } from "../config/constants";
import api from "../http-common";
import { mapActions,mapGetters } from "vuex";
export default {
  name: "Home",
  data(){
      return {
          show: false,
          componentKey: 0,
          homeOffer:{},
          is_offer: false,

      }
  },
  components: {
    SliderHome,
    FreeShippingSlider,
    FeaturedProductSlider,
    LatestProductSlider,
    CategorySlider,
    SellCTA,
  },
  methods: {
    ...mapActions("youtube", ["saveYTData"]),
    ...mapActions("product", ["saveCategories","saveFeaturedProducts","saveLatestProducts"]),
    ...mapGetters("account", ["getUserId"]),
    async getDashboard() {
      let myRoute =  GET_DASHBOARD.route;
      if (this.getUserId != null) {
        myRoute += "?user_id="+this.getUserId();
      }
      const response = await api.execute(GET_DASHBOARD, myRoute);
      if (response && response.success == 1) {
        this.saveYTData(response.data.videoId);
        this.saveCategories(response.data.categories);
        this.saveFeaturedProducts(response.data.featured_products);
        this.saveLatestProducts(response.data.recent_products);
        if(response.data.home_offer != null){
          this.homeOffer = response.data.home_offer;
          this.is_offer = true;
        }
        this.$toast.success(response.message);
        this.show = true;
        this.componentKey += 1;
      } else if (response && response.success == 0) {
        this.$toast.error(response.message);
      } else {
        this.$toast.error(ERROR_SOMETHING_WENT_WRONG);
      }
    },
  },
  mounted() {
    this.getDashboard();
  },
};
</script>
